@import-normalize; /* bring in normalize.css styles */

html {
  scroll-behavior: smooth;
}

body {
  background-color: #ebebeb;
  color: #2c2b29;
  font-family: -apple-system, BlinkMacSystemFont, "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #403730;
}

address {
  font-style: normal;
}

.swiper-pagination-bullet-active {
  background-color: #f6973f !important;
}

.swiper-scrollbar-drag {
  background-color: #f6973f !important;
}

.btnBox {
  display: flex;
  justify-content: center;
  padding: 32px 0px 87px 0px;
}

.btn {
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.08em;
  font-weight: 500;
  color: #f9f9f9;
  text-transform: uppercase;
  padding: 11px 23px;
  margin: 0 auto;
  background-color: #f6973f;
  outline: none;
  border: none;
  border-radius: 4px;
  cursor: none;
  display: block;

  &:active {
    box-shadow: inset 0px 0px 3px 3px rgba(0, 0, 0, 0.1),
      inset 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
    background-color: #dd8839;
  }
}

.sectionTitle {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  color: #f9f9f9;
  padding: 78px 0 20px 0;
}
.sectionTitle__LP {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  color: #2c2b29;
  padding: 0px 0 32px 0;
}
.sectionTitle__dark {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  color: #2c2b29;
  padding: 78px 0 20px 0;
}
.sectionTitle__ourHistory {
  position: absolute;
  margin-top: -52px;
  left: 18px;
  padding: 0;
}

.sectionSubtitle {
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 0.08em;
  font-weight: 500;
  color: #f9f9f9;
  padding-bottom: 12px;
}
.sectionSubtitle__ourHistory {
  padding-top: 48px;
}

.letter {
  color: #f6973f;
}

.sectionText {
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.15em;
  font-weight: 300;
  color: #f9f9f9;
  padding-bottom: 68px;
}

.container {
  max-width: 375px;
  margin: 0 auto;
}
